.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #000 0%, #0f2027 100%);
  animation: matrixBgMove 10s ease-in-out infinite alternate;
}

/* Animated gradient movement */
@keyframes matrixBgMove {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

/* Matrix rain overlay */
.App::before {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 0;
  background: repeating-linear-gradient(
    to bottom,
    rgba(57,255,20,0.07) 0px,
    rgba(57,255,20,0.13) 2px,
    transparent 4px,
    transparent 24px
  );
  opacity: 0.7;
  animation: matrixRainBg 2.5s linear infinite;
}

/* Subtle shimmer effect for the rain */
@keyframes matrixRainBg {
  0% { background-position-y: 0; }
  100% { background-position-y: 48px; }
}

/* Ensure content is above the background */
.App > * {
  position: relative;
  z-index: 1;
}
