.timer-container {
    background: #0f2027; /* deep dark */
    background: linear-gradient(135deg, #000 0%, #0f2027 100%);
    color: #39ff14;
    font-family: 'Share Tech Mono', 'Consolas', 'Courier New', monospace;
    padding: 2rem;
    border-radius: 16px;
    box-shadow: 0 0 40px #39ff1466;
    max-width: 400px;
    margin: 2rem auto;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.timer-owner {
    font-size: 2.2rem;
    color: #39ff14;
    margin-bottom: 0.5rem;
    text-shadow: 0 0 12px #39ff14, 0 0 24px #39ff14;
    letter-spacing: 0.15em;
}

.timer-title {
    font-size: 2rem;
    letter-spacing: 2px;
    margin-bottom: 1.5rem;
    text-shadow: 0 0 8px #39ff14, 0 0 16px #39ff14;
}

.timer-background {
    background: rgba(0, 0, 0, 0.2);
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    border-radius: 8px;
    box-shadow: 0 0 8px #39ff1433;
}

.timer-dager, .timer-timer, .timer-minutter, .timer-sekunder, .timer-millisekunder {
    font-size: 2.5rem;
    font-weight: bold;
    letter-spacing: 0.1em;
    text-shadow: 0 0 8px #39ff14, 0 0 16px #39ff14;
    margin: 0;
}

.timer-dager-label, .timer-timer-label, .timer-minutter-label, .timer-sekunder-label, .timer-millisekunder-label {
    font-size: 1rem;
    color: #aaffcc;
    text-shadow: 0 0 4px #39ff14;
    margin-bottom: 0.2rem;
}

/* Optional: Matrix rain effect (simple version) */
@keyframes matrixRain {
    0% { opacity: 0.2; }
    50% { opacity: 1; }
    100% { opacity: 0.2; }
}
.timer-container::before {
    content: "";
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    pointer-events: none;
    background: repeating-linear-gradient(
        to bottom,
        rgba(57,255,20,0.05) 0px,
        rgba(57,255,20,0.15) 2px,
        transparent 4px,
        transparent 24px
    );
    animation: matrixRain 2s linear infinite;
    z-index: 0;
}
.timer-container > * {
    position: relative;
    z-index: 1;
}